import React from "react";
import moment from "moment";
import { useWeb3React } from "@web3-react/core";
import web3 from "web3";

import { connectors } from "../utils/connectors";

import { truncateAddress } from "../utils";
import { CHAINS } from "../utils/chains";
import { IMessage } from "@/utils/definitions";

export default function Navigationt({ user, chain, setUser, friend, setFriend, messages, setMessages, contract, setMessageCount, isdark, setIsdark })
{
	const { active, library, activate, deactivate } = useWeb3React();

	async function handleSearchFriend(e)
	{
		if(e.key === "Enter" && e.target.value)
		{
			if(contract && web3.utils.isAddress(e.target.value))
			{
				let msgsFromUser;
				let msgsToUser;
				try
				{
					msgsFromUser = await contract.getMessagesFromUser(e.target.value);
				} catch(error)
				{
					console.error(error);
				}
				try
				{
					msgsToUser = await contract.getMessagesToUser(e.target.value);
				} catch(error)
				{
					console.error(error);
				}
				const msgs = [
					...msgsFromUser.map(msg => ({ timestamp: moment.unix(msg.timestamp.toNumber()), sender: msg.sender, receiver: msg.receiver, content: msg.content })),
					...msgsToUser.map(msg => ({ timestamp: moment.unix(msg.timestamp.toNumber()), sender: msg.sender, receiver: msg.receiver, content: msg.content })),
				];
				setMessageCount(msgs.length);
				msgs.sort((msg1, msg2) => msg1.timestamp.unix() - msg2.timestamp.unix());
				const groupedByDate = msgs.reduce((acc, msg: IMessage) =>
				{
					const date = msg.timestamp.format("YYYY-MM-DD");
					(acc[date] = acc[date] || []).push(msg);
					return acc;
				}, {});
				const messageArrays = Object.values(groupedByDate);
				setMessages(messageArrays);
				setFriend({
					account: e.target.value,
					truncated: truncateAddress(e.target.value),
				});
			}
			e.target.value = "";
		}
	}

	async function switchNetwork(CHAIN)
	{
		try
		{
			await library.provider.request({
				method: "wallet_switchEthereumChain",
				params: [
					{
						chainId: CHAIN.chainId,
					}
				],
			});
		}
		catch(switchError: any)
		{
			if(switchError.code === 4902)
			{
				try
				{
					await library.provider.request({
						method: "wallet_addEthereumChain",
						params: [
							{
								chainId: CHAIN.chainId,
								rpcUrls: [
									...CHAIN.rpcUrls,
								],
								chainName: CHAIN.chainName,
								nativeCurrency: {
									name: CHAIN.currency,
									decimals: CHAIN.decimals,
									symbol: CHAIN.symbol,
								},
								blockExplorerUrls: [
									...CHAIN.blockExplorerUrls,
								],
								iconUrls: [
									...CHAIN.iconUrls,
								],
							},
						],
					});
				}
				catch(error)
				{
					console.error(error);
				}
			}
		}
	};

	return (
		<React.Fragment>
			<div className="nav-left flex items-center gap-1">
				<button className="btn" onClick={() => { navigator.clipboard.writeText(user.account); }}><div className={`badge${active ? " badge-primary" : " badge-neutral"}`}></div>{active ? user.truncated : "Offline"}</button>
				{active && user.balance && <button className="btn" onClick={() => { user.balance && navigator.clipboard.writeText(user.balance.toString()); }}>{user.balance.toFixed(2)} {chain.symbol}</button>}
			</div>
			<div className="nav-center flex items-center">
				<label className="input input-bordered flex items-center" onKeyDown={handleSearchFriend}>
					<input type="text" className="grow" placeholder="Search Wallet..." />
					<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16" fill="currentColor" className="w-4 h-4 opacity-70"><path fillRule="evenodd" d="M9.965 11.026a5 5 0 1 1 1.06-1.06l2.755 2.754a.75.75 0 1 1-1.06 1.06l-2.755-2.754ZM10.5 7a3.5 3.5 0 1 1-7 0 3.5 3.5 0 0 1 7 0Z" clipRule="evenodd" /></svg>
				</label>
			</div>
			<div className="nav-right flex justify-between items-center gap-2">
				<label className="flex cursor-pointer gap-2">
					<svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><circle cx="12" cy="12" r="5" /><path d="M12 1v2M12 21v2M4.2 4.2l1.4 1.4M18.4 18.4l1.4 1.4M1 12h2M21 12h2M4.2 19.8l1.4-1.4M18.4 5.6l1.4-1.4" /></svg>
					<input aria-label="theme" type="checkbox" checked={isdark} onChange={() => setIsdark(!isdark)} value="dark" className="toggle theme-controller" />
					<svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><path d="M21 12.79A9 9 0 1 1 11.21 3 7 7 0 0 0 21 12.79z"></path></svg>
				</label>
				{!active ? (
					<React.Fragment>
						<button className="btn" onClick={() => document && (document.getElementById("connect-wallet-modal") as any).showModal()}>Connect</button>
						<dialog id="connect-wallet-modal" className="modal">
							<div className="modal-box">
								<form method="dialog">
									<button className="btn btn-sm btn-circle btn-ghost absolute right-2 top-2"></button>
								</form>
								<h3 className="font-bold text-lg">Select Wallet</h3>
								<button className="btn btn-outline btn-block my-1" onClick={() => { activate(connectors.Injected); }}><img src={`${process.env.PUBLIC_URL}/assets/images/mm.png`} width={25} height={25} alt="Metamask" />Metamask</button>
								<button className="btn btn-outline btn-block my-1" onClick={() => { activate(connectors.WalletConnect); }}><img src={`${process.env.PUBLIC_URL}/assets/images/wc.png`} width={26} height={26} alt="Wallet Connect" />Wallet Connect</button>
								<button className="btn btn-outline btn-block my-1" onClick={() => { activate(connectors.CoinbaseWallet); }}><img src={`${process.env.PUBLIC_URL}/assets/images/cbw.png`} width={25} height={25} alt="Coinbase Wallet" />Coinbase Wallet</button>
							</div>
							<form method="dialog" className="modal-backdrop">
								<button>close</button>
							</form>
						</dialog>
					</React.Fragment>
				) : (
					<React.Fragment>
						<div className="dropdown dropdown-hover">
							<div tabIndex={0} role="button" className="btn m-1">{chain.title}</div>
							<ul tabIndex={0} className="dropdown-content z-[1] menu p-2 shadow bg-base-100 rounded-box w-52">
								{CHAINS.map(CHAIN => <li key={CHAIN.id}><a onClick={() => switchNetwork(CHAIN)}><img src={`${process.env.PUBLIC_URL}/assets/images/${CHAIN.iconUrls[0]}`} width={15} height={15} alt={CHAIN.title} />{CHAIN.title}</a></li>)}
							</ul>
						</div>
						<button className="btn" onClick={deactivate}>Disconnect</button>
					</React.Fragment>
				)}
			</div>
		</React.Fragment>
	);
}