import { InjectedConnector } from "@web3-react/injected-connector";
import { WalletConnectConnector } from "@web3-react/walletconnect-connector";
import { WalletLinkConnector } from "@web3-react/walletlink-connector";

import { CHAINS } from "./chains";

const Injected: InjectedConnector = new InjectedConnector({
	supportedChainIds: [...CHAINS.map(chain => chain.id)],
});

const WalletConnect: WalletConnectConnector = new WalletConnectConnector({
	rpc: `https://mainnet.infura.io/v3/${process.env.INFURA_KEY}`,
	bridge: "https://bridge.walletconnect.org",
	qrcode: true,
});

const CoinbaseWallet = new WalletLinkConnector({
	supportedChainIds: [...CHAINS.map(chain => chain.id)],
	url: `https://mainnet.infura.io/v3/${process.env.INFURA_KEY}`,
	appName: "Document",
});

export const connectors = {
	Injected: Injected,
	WalletConnect: WalletConnect,
	CoinbaseWallet: CoinbaseWallet,
};
