import React, { useEffect, useState } from "react";
import moment from "moment";
import { ethers } from "ethers";
import { useWeb3React } from "@web3-react/core";

import Navigationt from "./components/Navigation";

import { truncateAddress } from "./utils";
import { CHAINS, ICHAIN } from "./utils/chains";
import { IMessage, IUser } from "./utils/definitions";

import "./App.scss";

import { EVMMessenger as IEVMMessenger } from "../types/ethers-contracts/EVMMessenger";
const EVMMessenger = require("./contracts/EVMMessenger.json");

export default function App()
{
	const { active, account, chainId, library } = useWeb3React();
	const [isdark, setIsdark] = useState(JSON.parse(String(localStorage.getItem("isdark"))));
	const [user, setUser] = useState<IUser>({
		account: account ?? "",
		truncated: truncateAddress(account) ?? "",
		balance: 0,
	});
	const [friend, setFriend] = useState<IUser>({
		account: "",
		truncated: "",
	});
	const [messages, setMessages] = useState<any[]>([]);
	const [messageCount, setMessageCount] = useState<number>(0);
	const [messageContent, setMessageContent] = useState<string>("");
	const [contract, setContract] = useState<IEVMMessenger>();
	const chain: ICHAIN = CHAINS.filter(chain => chain.id === chainId)[0];
	const [interactions, setInteractions] = useState<string[]>([]);

	useEffect(() =>
	{
		if(library && account)
		{
			const signer = library.getSigner(account);
			const currentContract = new ethers.Contract(chain.contracts[0].address, EVMMessenger.abi, signer) as IEVMMessenger;
			getUser();
			setContract(currentContract);
			getInteractions();
			const onMessageSent = async (sender, receiver, content, timestamp) =>
			{
				await handleGetMessages(friend.account);
			};
			currentContract.on("MessageSent", onMessageSent);
			return () =>
			{
				currentContract.off("MessageSent", onMessageSent);
			};
		}

		async function getBalance(): Promise<string>
		{
			return await library.getBalance(account);
		}

		async function getUser()
		{
			if(account)
			{
				const balance = await getBalance();
				setUser({
					...user,
					account: account,
					truncated: truncateAddress(account),
					balance: parseFloat(ethers.utils.formatEther(balance)),
				});
			}
		}

		async function getInteractions()
		{
			if(contract)
			{
				let inter;
				try
				{
					inter = await contract.getInteractions(user.account);
					setInteractions(inter);
				} catch(error)
				{
					console.error(error);
				}
			}
		}
	}, [library, account, user]);

	useEffect(() =>
	{
		localStorage.setItem("isdark", JSON.stringify(isdark));
	}, [isdark]);

	async function handleGetMessages(friendAccount)
	{
		setFriend({
			account: friendAccount,
			truncated: truncateAddress(friendAccount),
		});
		if(contract)
		{
			let msgsFromUser;
			let msgsToUser;
			try
			{
				msgsFromUser = await contract.getMessagesFromUser(friendAccount);
			} catch(error)
			{
				console.error(error);
			}
			try
			{
				msgsToUser = await contract.getMessagesToUser(friendAccount);
			} catch(error)
			{
				console.error(error);
			}
			const msgs = [
				...msgsFromUser.map(msg => ({ timestamp: moment.unix(msg.timestamp.toNumber()), sender: msg.sender, receiver: msg.receiver, content: msg.content })),
				...msgsToUser.map(msg => ({ timestamp: moment.unix(msg.timestamp.toNumber()), sender: msg.sender, receiver: msg.receiver, content: msg.content })),
			];
			setMessageCount(msgs.length);
			msgs.sort((msg1, msg2) => msg2.timestamp.unix() - msg1.timestamp.unix());
			const groupedByDate = msgs.reduce((acc, msg: IMessage) =>
			{
				const date = msg.timestamp.format("YYYY-MM-DD");
				(acc[date] = acc[date] || []).push(msg);
				return acc;
			}, {});
			const messageArrays = Object.values(groupedByDate);
			setMessages(messageArrays);
		}
	}

	async function handleSendMessage()
	{
		if(active && contract && friend.account && messageContent)
		{
			try
			{
				await contract.sendMessage(friend.account, messageContent);
				setMessageContent("");
			} catch(error)
			{
				console.error(error);
			}
		}
	}

	return (
		<React.Fragment>
			<div className="navigation flex justify-between p-2">
				<Navigationt
					user={user}
					chain={chain}
					setUser={setUser}
					friend={friend}
					setFriend={setFriend}
					contract={contract}
					setMessageCount={setMessageCount}
					isdark={isdark}
					setIsdark={setIsdark}
					messages={messages}
					setMessages={setMessages}
				/>
			</div>
			<div className="sidebar overflow-auto bg-base border-8 border-r-0 border-primary-content">
				<ul className="flex flex-col gap-1 p-1">
					{active && interactions.map(inter => <li key={inter} className={`btn w-full${inter === friend.account ? " btn-active" : ""}`} onClick={(e) => handleGetMessages(inter)}>{truncateAddress(inter)}</li>)}
				</ul>
			</div>
			<div className="window border-8 border-primary-content overflow-auto">
				{CHAINS.some(CHAIN => CHAIN.id === chainId) ?
					<div className="overflow-auto">
						{active && friend.account && (messages.length > 0) && messages.map((msgs, index1) => (
							<div key={`${user.truncated}${friend.truncated}${msgs[0].timestamp.format("DD/MM/YYYY")}${index1}`} className="mb-4">
								<time className="block text-xs opacity-100 text-center bg-primary-content bg-opacity-85">{msgs[0].timestamp.format("DD/MM/YYYY")}</time>
								{msgs.map((msg, index2) => (
									<div key={`${user.truncated}${friend.truncated}${msg.timestamp.unix()}${index2}`} className={`chat${(msg.sender === user.account) ? " chat-start" : " chat-end"} p-4`}>
										<div className="chat-header">{`${(msg.sender === user.account) ? user.truncated : friend.truncated}`}</div>
										<div className={`chat-bubble${(msg.sender === user.account) ? " chat-bubble-primary" : " chat-bubble-secondary"}`}>{msg.content}</div>
										<div className="chat-footer opacity-50">{msg.timestamp.format("HH:mm:ss")}</div>
									</div>
								))}
							</div>
						))}
					</div> :
					<div className="flex justify-center items-center">
						<div className="badge badge-outline badge-warning gap-2 p-4">
							<svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" className="inline-block w-4 h-4 stroke-current"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12"></path></svg>
							Unsupported Network
						</div>
					</div>
				}
				<div className="flex gap-1 p-1">
					<textarea placeholder="Message..." className="textarea textarea-bordered resize-none w-full" value={messageContent} onChange={(e) => setMessageContent(e.target.value)}></textarea>
					<button className="btn btn-neutral h-full" onClick={handleSendMessage}>Send</button>
				</div>
			</div>
			<div className="footer flex justify-between bg-transparent p-2">
				<span>{messageCount} Messages</span>
			</div>
		</React.Fragment >
	);
}